<template>
  <div :style="{'background-color':backgroundColor }" class="wr-disbounce__wrapper">
    <div class="wr-disbounce" ref="disbounce">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "wrDisbounce",

  props: {
    backgroundColor: {
      type: String,
      default: "#ffffff"
    }
  },

  data() {
    return {
        startPoint:''
    };
  },

  mounted() {
    this.disbounce = this.$refs["disbounce"];
    this.disbounce.addEventListener("touchstart", this.touchstartEvent);
    this.disbounce.addEventListener("touchmove", this.touchmoveEvent, {
      passive: false // passivefalse
    });
  },

  methods: {
    getPoint(e) {
      return {
        x: e.touches ? e.touches[0].pageX : e.clientX,
        y: e.touches ? e.touches[0].pageY : e.clientY
      };
    },

    touchstartEvent(e) {
      this.startPoint = this.getPoint(e);
    },

    touchmoveEvent(e) {
      if (!this.startPoint) return;

      

      const scrollTop = this.disbounce.scrollTop; // 
      const curPoint = this.getPoint(e);
      const moveY = curPoint.y - this.startPoint.y; // 

      // 
      if (moveY > 0) {
        // bounce
        if (scrollTop <= 0) e.preventDefault();
   
        // 
      } else {
        // const scrollHeight = this.disbounce.scrollHeight; // 
        // const clientHeight = this.disbounce.clientHeight; // 
        // const scrollBottom = scrollHeight - clientHeight - scrollTop; // 

        // // iosbounce
        // if (scrollBottom <= 0) e.preventDefault();
      }
    }
  }
};
</script>

<style scoped="scoped">
@import "./disbounce.scss";
</style>